.posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
}

.posters::-webkit-scrollbar {
  display: none;
}

.category {
  margin-left: 10px;
  color: white;
  margin-bottom: 20px;
}

.row {
  margin-top: 30px;
}

.row img {
  margin-right: 10px;
  width: 150px;
  max-height: 260px;
  transition: 450ms;
  cursor: pointer;
}

.row img:hover {
  transform: scale(1.08);
}
