.banner img {
  width: 100%;
  height: 450px;
}

.content {
  margin-top: -350px;
  color: white;
  margin-left: 30px;
}

.movieName {
  font-size: 34px;
  font-weight: 660;
  margin-bottom: 20px;
}

.button {
  cursor: pointer;
  color: white;
  border: none;
  font-weight: 700;
  width: 80px;
  height: 30px;
  margin-right: 1rem;
  background-color: rgba(51, 51, 51, 0.5);
}

.button:hover {
  color: black;
  background-color: white;
  transition: all 0.5s;
}

.description {
  margin-top: 20px;
  font-weight: 600;
  font-size: 15px;
  max-width: 360px;
  height: 80px;
}
